@tailwind base; 
@tailwind components; 
@tailwind utilities;

@layer utilities {
    :root {
        font-size: 16px;
        /* TODO: menu skal kun indeholde 1 række og helst være fontsize neutral => eget LOGO m. text svg. */
        --height-menu: 3rem; /* svarende til 3*fontsize */
        --height-control: 3.5rem; 
        --height-modal-head: 3rem;
        --modal-main-padding: 0.5rem;
        --height-draft-filter: 4rem; /* height-draft-filter1 + height-draft-filter2 + height-draft-filter3 */
        --height-draft-filter1: 1.8rem;
        --height-draft-filter2: 1.8rem;
        --height-draft-filter3: 0.4rem;
        --height-study-filter: 2.2rem;
        --height-study-filter1: 1.8rem;
        --height-study-filter2: 0.4rem;

        --height-trade-grid-head: 2rem;
        --height-trade-grid-foot: 2.5rem;
        --height-trade-confirm-grid-head: 2rem;
        --height-trade-confirm-grid-foot: 2.5rem;
        --height-trade-validate-grid-head: 2rem;
        --height-trade-validate-grid-foot: 2.5rem;
        /* --height-trade-partner-grid-head: 2rem; */
        --height-trade-partner-grid-item: 3rem;

        --modal-border-x: 0.25rem;

        --dev-modal-width: 100vh; /* 360px; */
        --dev-modal-margin-l: 0px; /* 100px; */
    }

    /* === No scrollbar but still scroll === */
    .no-scrollbar::-webkit-scrollbar { /* Chrome, Safari and Opera */
        display: none;
    } 
    .no-scrollbar {
        -ms-overflow-style: none; /* IE and Edge */
        scrollbar-width: none; /* Firefox */
    }
    .view-scrollable{
        height: 100%; 
        overflow-y: auto; 
        overflow-x: hidden;    
    }
    
    /* === TEMP!!! === */
    .dev-mobile {
        width: var(--dev-modal-width);
        margin-left: var(--dev-modal-margin-l);
    } 
    
    /* === Layout and Simulator === */   
    .sim {
        position: relative;
        height: calc(100vh);
    }
    .control {
        position: absolute;
        bottom: 0px;
        width: 100%;
        height: var(--height-control);
        @apply bg-slate-300 text-slate-700;
    }
    .view {
        position: absolute;
        top: var(--height-menu);
        width: 100%;
        height: calc(100vh - var(--height-menu) - var(--height-control));
        @apply bg-slate-100 text-slate-700;
    }
    

    /* === Buttons === */
    .btn {
        @apply font-semibold py-2 px-2 border rounded
            uppercase text-sm
            text-slate-300 hover:text-slate-300 
            bg-slate-700 hover:bg-slate-900
            border-slate-300 hover:border-slate-300
            disabled:bg-transparent disabled:text-slate-400 disabled:border-slate-400 disabled:shadow-none;
    }

    /* === Images === */
    .img-center {
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    .teamimg{
        align-items: center;
        display: flex;
        flex-flow: row nowrap;
    }
    
    /* === Text === */
    .txt-siz-u-2{
        @apply text-xl;
    }
    .txt-siz-u-1{
        @apply text-lg;
    }
    .txt-siz{
        @apply text-base;
    }
    .txt-siz-d-1{
        @apply text-sm;
    }
    .txt-siz-d-2{
        @apply text-xs;
    }
    
    .txt-col-u-2{
        @apply  text-slate-900;
    }
    .txt-col-u-1{
        @apply  text-slate-800;
    }
    .txt-col{
        @apply  text-slate-700;
    }
    .txt-col-d-1{
        @apply  text-slate-600;
    }
    .txt-col-d-2{
        @apply  text-slate-500;
    }
    .txt-col-highlight{
        @apply  text-sky-700;
    }

    /* =================== Modal ================== */
    .modal {
        position: fixed;
        top: 0;
        left: 0;
        /* width:100%; */
        height: 100%;
        background: rgba(0, 0, 0, 0.5);

        /* dev-mobile */
        width: var(--dev-modal-width);
        margin-left: var(--dev-modal-margin-l);
    }

    .modal-main {
        position:fixed;
        background: white;

        /* dev-mobile */
        width: calc(var(--dev-modal-width) - var(--modal-border-x) - var(--modal-border-x));
        margin-left: calc(var(--dev-modal-margin-l) + var(--modal-border-x));

        /* custom adjustments */
        top: var(--height-menu);
        left: 0;
        height: calc(100vh - var(--height-menu) - var(--height-control));
        border-radius: 4px;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        padding: var(--modal-main-padding);

        /* width: 80%;
        height: auto;
        top:50%;
        left:50%; */
        /* transform: translate(-50%,-50%); */
    }

    .modal-contaier{
        position: relative;
        height: 100%;
        width: 100%;
        /* border: 1px solid blue; */
    }
    .modal-head{   
        position: relative;
        height: var(--height-modal-head);
        width: 100%;
        /* background-color: fuchsia; */
    }
        .modal-item-closebtn{
            position: absolute;
            top: 0px;
            right: 0px;
            color: grey;
            line-height: 0.75rem; /* to move the fa-icon up */
        }
        .modal-item-title{
            position: absolute;
            top: 0px;
            left: 0px;
            @apply txt-siz-u-2 font-bold txt-col-highlight;
        }
    .modal-item-body{
        position: absolute;
        top: var(--height-modal-head);
        left: 0rem;
        width: 100%;
    }
        .modal-cancel-btn{
            @apply font-semibold py-1 px-1 border rounded
                uppercase text-xs
                text-slate-300 hover:text-slate-700 
                bg-slate-700 hover:bg-slate-300
                border-slate-300 hover:border-slate-700
                disabled:bg-red-50 disabled:text-red-500 disabled:border-red-200 disabled:shadow-none;
        }
        .modal-ok-btn{
            @apply font-semibold py-1 px-1 border rounded
                uppercase text-xs
                text-white hover:text-white 
                bg-slate-600 hover:bg-slate-800
                border-transparent hover:border-transparent
                disabled:shadow-none;
        }
        .modal-transparent-btn{
            @apply py-1 px-1 text-slate-700 hover:text-slate-900 bg-transparent hover:bg-transparent;
        }

    .modal-display-block {
        display: block;
    }

    .modal-display-none {
        display: none;
    } 

    
    /* ============================================================================================= */
    /* === DRAFT BOARD === */
    .draftboard{
    }
    .draftboarditemheader{
        @apply  grid grid-rows-1 grid-flow-col gap-0
                bg-slate-700 text-slate-300;
        grid-template-columns: 1fr;            
    }
    .draftboarditem{
        @apply border-b border-slate-300;
        display: grid;
        grid-template-areas: 
            'i-no i-logo i-txt i-pos'
            'i-no2 i-logo i-txt2 i-pos2';
        grid-template-columns: 3rem 3rem 1fr 3rem;
        grid-template-rows: 1.8rem 1.2rem;
    }
    .draftboarditem-no-player{
        @apply border-b border-slate-300;
        display: grid;
        grid-template-areas: 
            'i-no i-logo i-needs'
            'i-no2 i-logo i-needs';
        grid-template-columns: 3rem 3rem 1fr;
        grid-template-rows: 1.8rem 1.2rem;
    }
    .draftboarditem-no{
        grid-area: i-no; 
        @apply txt-col-u-2 txt-siz-u-2 text-center self-end;
    }
    .draftboarditem-no2{
        grid-area: i-no2; 
        @apply txt-col-d-2 txt-siz-d-2 text-center self-start;
    }
    .draftboarditem-logo{
        grid-area: i-logo; 
        @apply flex align-middle justify-center;
    }
    .draftboarditem-txt{
        grid-area: i-txt; 
        @apply txt-col-highlight txt-siz font-semibold truncate self-end pl-2;
    }
    .draftboarditem-txt2{
        grid-area: i-txt2; 
        @apply txt-col-d-2 txt-siz-d-2 self-start pl-2;
    }
    .draftboarditem-pos{
        grid-area: i-pos; 
        @apply txt-col-u-2 txt-siz-u-2 text-center self-end;
    }
    .draftboarditem-pos2{
        grid-area: i-pos2; 
        @apply txt-col-d-2 txt-siz-d-2 text-center self-start;
    }
    .draftboarditem-txt-needs{
        grid-area: i-txt; 
        @apply txt-col-d-2 txt-siz-d-2 truncate self-end pl-2;
    }
    .draftboarditem-txt2-picked{
        grid-area: i-txt2; 
        @apply txt-col-d-2 txt-siz-d-2 self-start pl-2;
    }
    .draftboarditem-txt-needs{
        grid-area: i-needs; 
        @apply txt-col-d-2 txt-siz-d-2 self-center pl-2;
    }

    /* === DRAFT === */
    .draft-container{
        position: relative;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding));
        width: 100%;
        /* @apply bg-slate-100 text-slate-700; */
    }  
    .btnPos{
        /* w-1.5 h-1.5 justify-center border border-slate-800 */
        @apply font-semibold py-2 px-2 border rounded
        uppercase text-xs self-center w-20
        text-slate-300 hover:text-slate-700 
        bg-slate-700 hover:bg-slate-300
        border-slate-300 hover:border-slate-700
        disabled:bg-red-50 disabled:text-red-500 disabled:border-red-200 disabled:shadow-none;
    }
    .draft-list{
        position: absolute;
        top: var(--height-draft-filter);
        left: 0rem;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding) - var(--height-draft-filter));
        width: 100%;
        @apply bg-slate-100 text-slate-700 border border-slate-300;
        /* background-color: purple; */
    }
    .draft-list-item{
        @apply border-b border-slate-300;
        display: grid;
        grid-template-areas: 
            'dli-no dli-logo dli-txt dli-btn'
            'dli-no2 dli-logo dli-txt2 dli-btn';
        grid-template-columns: 3rem 3rem 1fr 4rem;
        grid-template-rows: 1.8rem 1.2rem;

        /* @apply  grid grid-rows-5 grid-flow-col gap-0
                border-b border-slate-300;
        grid-template-columns: 3rem 1fr 3rem;             */

        /* line-height: 1.44; */
        /* text-rendering: optimizeLegibility; */
        /* box-shadow: 0 -0.0625rem 0 #c9ced4; */
    }
    .draft-list-item-no{
        grid-area: dli-no; 
        @apply txt-col-u-2 txt-siz-u-2 text-center self-end;
    }
    .draft-list-item-no2{
        grid-area: dli-no2; 
        @apply txt-col-d-2 txt-siz-d-2 text-center self-start;
    }
    .draft-list-item-logo{
        grid-area: dli-logo; 
        @apply flex align-middle justify-center;
    }
    .draft-list-item-txt{
        grid-area: dli-txt; 
        @apply txt-col-highlight txt-siz font-semibold truncate self-end pl-2;
    }
    .draft-list-item-txt2{
        grid-area: dli-txt2; 
        @apply txt-col-d-2 txt-siz-d-2 self-start pl-2;
    }
    .draft-list-item-btn{
        grid-area: dli-btn; 
        @apply flex align-middle justify-center txt-col-u-2 txt-siz-u-2;
    }

    /* === DRAFT FILTERING === */
    .draft-filter{
        position: absolute;
        height: var(--height-draft-filter);
        width: 100%;
        display: grid;
        grid-template-areas: 
            'df-exp df-items'
            'df-exp2 df-items2'
            'df-gab df-gab';
        grid-template-columns: 2rem 1fr;
        grid-template-rows: var(--height-draft-filter1) var(--height-draft-filter2) var(--height-draft-filter3);        
        /* background-color: lightskyblue; */
    }
    .draft-filter-exp { 
        /* height: 1.5rem;
        width: 1.5rem; */
        grid-area: df-exp; 
        @apply text-xs font-semibold self-center;
    }
    .draft-filter-exp2 { /* not used */
        grid-area: df-exp2; 
    }
    .draft-filter-items { 
        grid-area: df-items; 
        @apply self-center justify-end;
        display: flex;
        justify-content: space-between;   
    }
    .draft-filter-items2 { 
        grid-area: df-items2; 
        @apply self-center justify-end;
        display: flex;
        justify-content: center;   
    }
    .draft-filter-btn2{
        @apply ml-1 mr-1;
    }
    .draft-filter-toggle{
        height: 1.5rem;
        width: 4.2rem;
        @apply py-1 px-2 border rounded self-center
        text-xs font-semibold text-center;
    }
    .draft-filter2-toggle{
        height: 1.5rem;
        width: 2.2rem;
        @apply py-1 px-2 border rounded self-center
        text-xs font-semibold text-center;
    }
    .draft-filter-toggle-off{
        @apply bg-transparent text-slate-500 border-slate-500;
    }
    .draft-filter-toggle-on{
        @apply bg-slate-500 text-white border-transparent;
    }
    .draft-filter-search{
        height: 1.5rem;
        width: 2rem;
        @apply text-xs bg-transparent text-slate-500 border-slate-500
               appearance-none border rounded w-full py-1 px-2 focus:outline-none
               leading-tight; /* shadow focus:outline-none focus:shadow-outline */
    }  

    /* === TRADE === */
    .trade-grid{
        position: relative;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding));
        width: 100%;
        @apply bg-transparent text-slate-700;
        display: grid;
        grid-template-areas:
            'head head head head'
            'ac af bc bf'
            'foot foot foot foot';
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: var(--height-trade-grid-head) 1fr var(--height-trade-grid-foot);
    }
    .trade-area-head{
        grid-area: head;
        position: absolute;
        height: var(--height-trade-grid-head);
        width: 100%;
        display: grid;
        grid-template-areas: 'a b';
        grid-template-columns: 1fr 1fr;
        grid-template-rows: var(--height-trade-grid-head);
    }
    .trade-area-foot{
        grid-area: foot;
        position: absolute;
        height: var(--height-trade-grid-foot);
        width: 100%;
        display: flex;
        justify-content: space-between;   
    }
    .trade-grid-col{
        position: absolute;
        top: 0px; /* <--- IMPORTANT*/
        left: 0px;
        width: 100%;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding) - var(--height-trade-grid-head) - var(--height-trade-grid-foot));
    }
    .trade-head-area-a { 
        grid-area: a; 
        @apply border-b border-slate-300;
    }
    .trade-head-area-b { 
        grid-area: b; 
        @apply border-b border-l border-slate-300;
        justify-content: space-between;   
    }
    .trade-area-a-current { 
        grid-area: ac; 
        @apply trade-grid-col view-scrollable no-scrollbar;
    }
    .trade-area-a-future { 
        grid-area: af; 
        @apply trade-grid-col view-scrollable no-scrollbar;
    }
    .trade-area-b-current { 
        grid-area: bc; 
        @apply border-l border-slate-300 pl-1; 
        @apply trade-grid-col view-scrollable no-scrollbar;
    }
    .trade-area-b-future { 
        grid-area: bf; 
        @apply  pl-1;
        @apply trade-grid-col view-scrollable no-scrollbar;
    }    


    /* === TRADE CONFIRM === */
    .trade-confirm-grid{
        position: relative;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding));
        width: 100%;
        @apply bg-transparent text-slate-700;
        display: grid;
        grid-template-areas:
            'tc-head tc-head'
            'tc-a tc-b'
            'tc-foot tc-foot';
        grid-template-columns: 1fr 1fr;
        grid-template-rows: var(--height-trade-confirm-grid-head) 1fr var(--height-trade-confirm-grid-foot);
    }
    .trade-confirm-area-head{
        grid-area: tc-head;
        position: absolute;
        height: var(--height-trade-confirm-grid-head);
        width: 100%;
        display: grid;
        grid-template-areas: 'tc-a-head tc-b-head';
        grid-template-columns: 1fr 1fr;
        grid-template-rows: var(--height-trade-confirm-grid-head);
   }
    .trade-confirm-area-foot{
        grid-area: tc-foot;
        position: absolute;
        height: var(--height-trade-confirm-grid-foot);
        width: 100%;
        display: flex;
        justify-content: space-between;   
    }
    .trade-confirm-grid-col{
        position: absolute;
        top: 0px; /* <--- IMPORTANT*/
        left: 0px;
        width: 100%;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding) - var(--height-trade-confirm-grid-head) - var(--height-trade-confirm-grid-foot));
    }
    .trade-confirm-head-area-a { 
        grid-area: tc-a-head; 
        @apply border-b border-slate-300;
    }
    .trade-confirm-head-area-b { 
        grid-area: tc-b-head; 
        @apply border-b border-l border-slate-300;
    }
    .trade-confirm-area-a { 
        grid-area: tc-a; 
        @apply trade-confirm-grid-col view-scrollable no-scrollbar;
    }
    .trade-confirm-area-b { 
        grid-area: tc-b; 
        @apply border-l border-slate-300 pl-1; 
        @apply trade-confirm-grid-col view-scrollable no-scrollbar;
    }


    /* === TRADE VALIDATE === */
    .trade-validate-grid{
        position: relative;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding));
        width: 100%;
        @apply bg-transparent text-slate-700;
        display: grid;
        grid-template-areas:
            'tv-head'
            'tv-body'
            'tv-foot';
        grid-template-columns: 1fr;
        grid-template-rows: var(--height-trade-validate-grid-head) 1fr var(--height-trade-validate-grid-foot);
    }
    .trade-validate-area-head{
        grid-area: tv-head;
        position: absolute;
        height: var(--height-trade-validate-grid-head);
        width: 100%;
        display: flex;
   }
    .trade-validate-area-foot{
        grid-area: tv-foot;
        position: absolute;
        height: var(--height-trade-validate-grid-foot);
        width: 100%;
        display: flex;
    }
    .trade-validate-grid-col{
        position: absolute;
        top: 0px; /* <--- IMPORTANT*/
        left: 0px;
        width: 100%;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding) - var(--height-trade-validate-grid-head) - var(--height-trade-validate-grid-foot));   
    }   
    .trade-validate-area-body { 
        grid-area: tv-body; 
        @apply trade-validate-grid-col;
        display: grid;
        grid-template-areas:
            'tv-body-1'
            'tv-body-2';
        grid-template-columns: 1fr;
        /* grid-template-rows: 1rem 1fr; */
    }
    .trade-validate-area-body-1 { 
        grid-area: tv-body-1;
        @apply mt-8 text-2xl font-bold flex justify-center align-middle; 
    }
    .trade-validate-area-body-2 { 
        grid-area: tv-body-2; 
        @apply mt-4 flex justify-center; 
    }

    /* === TRADE PARTNER === */
    .trade-partner-grid{
        position: relative;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding));
        width: 100%;
        @apply bg-transparent text-slate-700;
        display: grid;
        grid-template-areas:
            'tf-b';
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }
    .trade-partner-body-area{
        grid-area: tf-b;
        position: absolute;
        height: 100%;
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        @apply view-scrollable no-scrollbar;
        grid-gap: 5px 5px;
    }
    .trade-partner-body-area-item {
        height: var(--height-trade-partner-grid-item);
        width: 100%;
        /* @apply bg-zinc-400; */
        display: grid;
        grid-template-areas:
            'item-logo item-logo'
            'item-pickno item-notify';
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 2rem 1.5rem;
        @apply border border-orange-900;
    }

    .trade-partner-body-area-item-logo {
        grid-area: item-logo; 
        @apply text-xs;
    }
    .trade-partner-body-area-item-pickno {
        grid-area: item-pickno; 
        @apply text-xs;
    }
    .trade-partner-body-area-item-notify {
        grid-area: item-notify; 
        @apply text-xs;
    }

    /* ============================================================================================= */
    /* === NAVBAR MENU - Part of Layout === */
    .prod-header {
        height: var(--height-menu);
        width: 100%;
        @apply bg-slate-300 text-slate-700;
        display: grid;
        grid-template-areas: 'p-logo p-kpi1 p-kpi2 p-kpi3 p-kpi4 p-burger';
        grid-template-columns: 1fr 3rem 3rem 3rem 3rem 3rem;
        grid-template-rows: var(--height-menu);
   }
   .prod-header-logo {
        grid-area: p-logo; 
        @apply text-xs;
    }
    .prod-header-kpi1 {
        grid-area: p-kpi1; 
        @apply text-xs;
    }
    .prod-header-kpi2 {
        grid-area: p-kpi2; 
        @apply text-xs;
    }
    .prod-header-kpi3 {
        grid-area: p-kpi3; 
        @apply text-xs;
    }
    .prod-header-kpi4 {
        grid-area: p-kpi4; 
        @apply text-xs;
    }
    .prod-header-burger {
        grid-area: p-burger; 
        @apply text-xs;
    }

    /* ============================================================================================= */
    /* === STUDY === */
    .study-container{
        position: relative;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding));
        width: 100%;
    }  
    .study-list{
        position: absolute;
        top: var(--height-study-filter);
        left: 0rem;
        height: calc(100vh - var(--height-menu) - var(--height-control) - var(--height-modal-head) - var(--modal-main-padding) - var(--modal-main-padding) - var(--height-study-filter));
        width: 100%;
        @apply bg-slate-100 text-slate-700 border border-slate-300;
        /* background-color: purple;  */
    }
    /* === STUDY FILTERING === */
    .study-filter{
        position: absolute;
        height: var(--height-study-filter);
        width: 100%;
        display: grid;
        /* grid-template-areas: 
            'df-exp df-items'
            'df-exp2 df-items2'
            'df-gab df-gab'; */
        /* grid-template-columns: 2rem 1fr; */
        /* grid-template-columns: 1fr;
        grid-template-rows: var(--height-study-filter1) var(--height-study-filter2) var(--height-study-filter3);         */
        grid-template-areas: 
            'df-items'
            'df-gab';
        grid-template-columns: 1fr;
        grid-template-rows: var(--height-study-filter1) var(--height-study-filter2);        
        /* background-color: lightskyblue; */
    }
    /* .study-filter-exp { 
        grid-area: df-exp; 
        @apply text-xs font-semibold self-center;
    }
    .study-filter-exp2 { 
        grid-area: df-exp2; 
    } */
    .study-filter-items { 
        grid-area: df-items; 
        @apply self-center justify-end;
        display: flex;
        justify-content: center;   
    }
    /* .study-filter-items2 { 
        grid-area: df-items2; 
        @apply self-center justify-end;
        display: flex;
        justify-content: center;   
    } */
    .study-filter-btn2{
        @apply ml-1 mr-1;
    }
    .study-filter-toggle{
        height: 1.5rem;
        /* width: 4.2rem; */
        @apply py-1 px-2 border rounded self-center text-xs font-semibold text-center;
    }
    /* .study-filter2-toggle{
        height: 1.5rem;
        @apply py-1 px-2 border rounded self-center
        text-xs font-semibold text-center;
    } */
    .study-filter-toggle-off{
        @apply bg-transparent text-slate-500 border-slate-500;
    }
    .study-filter-toggle-on{
        @apply bg-slate-500 text-white border-transparent;
    }

    /* ============================================================================================= */
    /* === STUDY NEED LIST === */
    .study-needlist-item{
        @apply border-b border-slate-300;
        display: grid;
        grid-template-areas: 
            'snl-logo snl-need-no snl-draft-no snl-remain-no snl-pick-no snl-arrow'
            'snl-logo snl-need-no2 snl-draft-no2 snl-remain-no2 snl-pick-no2 snl-arrow';
        grid-template-columns: 3rem 3rem 3rem 3rem 3rem 1fr;
        grid-template-rows: 1.8rem 1.2rem;
   }
   .study-needlist-item-logo{
        grid-area: snl-logo; 
        @apply flex align-middle justify-center;
    }
    .study-needlist-item-need-no{
        grid-area: snl-need-no; 
        @apply txt-col-u-2 txt-siz-u-2 text-center self-end;
    }
    .study-needlist-item-need-no2{
        grid-area: snl-need-no2; 
        @apply txt-col-d-2 txt-siz-d-2 text-center self-start;
    }
    .study-needlist-item-draft-no{
        grid-area: snl-draft-no; 
        @apply txt-col-u-2 txt-siz-u-2 text-center self-end;
    }
    .study-needlist-item-draft-no2{
        grid-area: snl-draft-no2; 
        @apply txt-col-d-2 txt-siz-d-2 text-center self-start;
    }
    .study-needlist-item-remain-no{
        grid-area: snl-remain-no; 
        @apply txt-col-u-2 txt-siz-u-2 text-center self-end;
    }
    .study-needlist-item-remain-no2{
        grid-area: snl-remain-no2; 
        @apply txt-col-d-2 txt-siz-d-2 text-center self-start;
    }
    .study-needlist-item-pick-no{
        grid-area: snl-pick-no; 
        @apply txt-col-u-2 txt-siz-u-2 text-center self-end;
    }
    .study-needlist-item-pick-no2{
        grid-area: snl-pick-no2; 
        @apply txt-col-d-2 txt-siz-d-2 text-center self-start;
    }
    .study-needlist-item-btn {
        grid-area: snl-arrow; 
        @apply flex align-middle justify-center txt-col-u-2 txt-siz-u-2;
    }
}